<!--审核视屏-->
<template>
  <div class="home">
    <!-- 顶部选择框-->
    <div class="homeHeader">
      <div class="HeaderLeft">
        <el-button type="primary" @click="getAuditVideoList(-1)">获取审核列表</el-button>
      </div>
      <div class="HeaderRight">
        <el-button type="primary" @click="getAuditVideoList(0)">未审核</el-button>
        <el-button type="primary" @click="getAuditVideoList(1)">审核通过</el-button>
        <el-button type="primary" @click="getAuditVideoList(2)">审核不通过</el-button>
      </div>


    </div>

    <!--中间部分-->
    <el-table :data="tableData" style="width: 100%">
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column prop="examineId" label="审核ID" width="100"></el-table-column>
      <el-table-column prop="name" label="视屏名称" width="100"></el-table-column>
      <el-table-column prop="introduction" label="视屏介绍" width="100"></el-table-column>
      <el-table-column prop="authorNickname" label="上传作者" width="150"></el-table-column>
      <el-table-column prop="typeName" label="视屏种类"></el-table-column>
      <el-table-column prop="passDate" label="上传时间"></el-table-column>
      <el-table-column prop="applyNickname" label="审核人"></el-table-column>
      <el-table-column prop="reviewComment" label="审核信息"></el-table-column>
      <el-table-column label="操作" width="150">
        <template slot-scope="scope">
          <el-button type="success" icon="el-icon-check" size="mini" circle @click="auditRow(scope.row)"></el-button>
          <el-button type="primary" icon="el-icon-edit" size="mini" circle @click="editRow(scope.row)"></el-button>
          <el-button type="danger" icon="el-icon-delete" size="mini" circle @click="deleteTable(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--分页表单-->
    <el-pagination background layout="prev, pager, next" :total="total" :page-size="pageSize"
      @current-change="handleCurrentChange" class="pageBody">
    </el-pagination>

    <!--审核视屏的弹窗-->
    <auditVideo ref="auditVideo"></auditVideo>
  </div>  
</template>

<script>
import auditVideo from '@/components/popUp/auditVideo'
import { getVideoList, getExamineList } from '../../services/videoManage'
export default {
  components: { auditVideo },
  name: 'examineManage',
  data() {
    return {
      tableData: [],
      total: 0, //页面总条数
      page: 1, //请求第一次的params
      pageSize: 0,
      listType: 0,   //0表示的是查看全部的视屏列表
      statusType:0   //获取审核视屏的状态
    }
  },
  mounted() {
    this.getAuditVideoList(-1)
  },
  methods: {

    //获取全部视屏的审核列表
    getAuditVideoList(status) {
      //修改展示数据的类型 
      this.listType = 1
      let page = this.page
      //保存审核状态 
      this.statusType = status
      getExamineList({status,page}).then((res) => {
        console.log(res);
        this.page = 1
        this.tableData = res.data.data.list
        this.total = res.data.data.total; //后端返回的总数量
        this.pageSize = res.data.data.pageSize //后端返回的每一页的总数
      })
    },
    //分页
    handleCurrentChange(page) {
      this.page = page;
      this.getAuditVideoList(this.statusType)
    },
    //审核视屏 
    auditRow(row){
      this.$refs.auditVideo.showDialog(row)
    },
    // 删除
    deleteTable(row) {
      console.log(row)
    },
    // 编辑表格
    editRow(row) {
      this.$refs.ConstitutionAAssess.show(row)
    },

  }

}
</script>

<style scoped lang="less">
.homeHeader {
  margin: 15px 10px 5px 10px;
  display: flex;
  justify-content: space-between;
  .HeaderLeft{
    margin-left: 10px;
  }
  .HeaderRight{
    margin-right: 10px;
  }
}

.pageBody {
  margin-top: 15px;
  display: flex;
  justify-content: center;
  align-content: center;
}
</style>
